import { ElementRect, OffsetPosition } from '@progress/kendo-popup-common';

const eitherRect = (rect: ElementRect, offset: OffsetPosition): ElementRect => {
    if (!rect) {
        return { height: 0, left: offset.left, top: offset.top, width: 0 } as ElementRect;
    }

    return rect;
};

const isDifferentOffset = (oldOffset: OffsetPosition, newOffset: OffsetPosition): boolean => {
    const { left: oldLeft, top: oldTop } = oldOffset;
    const { left: newLeft, top: newTop } = newOffset;

    return oldLeft !== newLeft || oldTop !== newTop;
};

const isDocumentAvailable = (): boolean => {
    return typeof document !== 'undefined';
};

const isWindowAvailable = (): boolean => {
    return typeof window !== 'undefined';
};

const hasBoundingRect = (elem: HTMLElement): boolean => !!elem.getBoundingClientRect;

const OVERFLOW_REGEXP = /auto|scroll/;

const scrollableParents = (element: HTMLElement): Array<HTMLElement> => {
    const parents = [];

    if (!isDocumentAvailable() || !isWindowAvailable()) { return parents; };

    let parent = element.parentElement;

    while (parent) {
        if (OVERFLOW_REGEXP.test(window.getComputedStyle(parent).overflow)) {
            parents.push(parent);
        }

        parent = parent.parentElement;
    }

    parents.push(window);

    return parents;
};

const FRAME_DURATION = 1000 / 60; //1000ms divided by 60fps

export {
    eitherRect,
    FRAME_DURATION,
    hasBoundingRect,
    isDifferentOffset,
    isDocumentAvailable,
    isWindowAvailable,
    scrollableParents
};



